import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { Box, Grid, ResponsiveContext, Text, Button } from "grommet";
import { Chat, Copy } from "grommet-icons";

import Card from "../../components/Card";
import { navTo } from "../nav/navStore";

const DashSide = () => {
  const { messages } = useSelector((state) => state.dash);
  const { files } = useSelector((state) => state.docs);
  const dispatch = useDispatch();

  return (
    <>
      <Box pad={{ top: "xlarge" }} />
      <ResponsiveContext.Consumer>
        {(size) => (
          <Grid
            gap="medium"
            columns={
              size === "small"
                ? { count: "fit", size: "small" }
                : { count: "fill", size: "full" }
            }
            justify="center"
            responsive
          >
            <Card
              title=""
              subTitle=""
              align="start"
              children={
                <Button
                  onClick={() => {
                    dispatch(navTo({ main: "messages", side: "dash" }));
                  }}
                >
                  <Chat size="large" />
                  <Text>{" " + messages.length}</Text>
                </Button>
              }
              footer=""
              size="small"
            />
            <Card
              title=""
              subTitle=""
              align="start"
              children={
                <Button
                  onClick={() => {
                    dispatch(navTo({ main: "dashdocs", side: "dash" }));
                  }}
                >
                  <Copy size="large" />
                  <Text>{" " + files.length}</Text>
                </Button>
              }
              footer=""
              size="small"
            />
          </Grid>
        )}
      </ResponsiveContext.Consumer>
    </>
  );
};

export default DashSide;
