import { useState, useEffect } from "react";
import { Document as PdfDocument, Page } from 'react-pdf/dist/esm/entry.webpack'
// pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

export const Document = ({ pdf, turnPage }) => {
  const [numPages, setNum] = useState(null);
  const [pageNumber, setPage] = useState(1);
  const [error, setError] = useState(null);

  useEffect(() => {
    switch (turnPage) {
      case true:
        return pageNumber < numPages ? setPage(pageNumber + 1) : null;
      case false:
        return pageNumber > 1 ? setPage(pageNumber - 1) : null;
      default:
        return null;
    }
  }, [turnPage, numPages, pageNumber]);

  const onDocumentLoadSuccess = ({ numPages }) => {
    setNum(numPages);
    setError(null);
  };

  return pdf ? (
    <PdfDocument
      file={pdf}
      onLoadSuccess={onDocumentLoadSuccess}
      onLoadError={setError}
    >
      {!error ? <Page pageNumber={pageNumber} /> : null}
    </PdfDocument>
  ) : null;
};
