import { useSelector } from "react-redux";
import React, { useState } from "react";
import { Box, DataTable, Text } from "grommet";

export const Messages = () => {
  const [select, onSelect] = useState(null);
  const { messages } = useSelector((state) => state.dash);

  const columns = {
    messages: [
      {
        property: "body",
        primary: true,
        header: <Text>Messages</Text>,
        render: (datum) => datum.body.substring(0, 40),
      },
    ],
  };
  return (
    <Box align="center">
      <DataTable
        columns={columns.messages}
        data={messages}
        step={10}
        select={select}
        onSelect={onSelect}
        onClickRow={() => null}
      />
    </Box>
  );
};
