import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Document } from "../../components/Document";
import Card from "../../components/Card";
import { showDoc } from "../dash/dashStore";
import { navTo } from "../nav/navStore";
import { Grid, Box, Button, Anchor, Text } from "grommet";
import { Chat, Close, Previous, Next } from "grommet-icons";

export const DashDoc = () => {
  const dispatch = useDispatch();
  const { showdoc } = useSelector((state) => state.dash);
  const [turnPage, setTurnPage] = useState(null);
  useEffect(() => {
    setTurnPage(null);
  }, [turnPage]);
  return (
    <Box align="center" pad={{ top: "large" }}>
      <Box direction="row">
        <Button icon={<Previous />} onClick={() => setTurnPage(false)} />
        <Button icon={<Next />} onClick={() => setTurnPage(true)} />
        <Box width="medium" />
        <Button
          icon={<Close />}
          onClick={() => {
            dispatch(navTo({ main: "dash", side: "dash" }));
            dispatch(showDoc(null));
          }}
        />
      </Box>
      <Box pad="medium">
        <Document pdf={showdoc} turnPage={turnPage} />
      </Box>
    </Box>
  );
};

export const DashDocs = () => {
  const dispatch = useDispatch();
  const { files } = useSelector((state) => state.docs);

  return (
    <Grid align="center" gap="medium" columns={{ count: "fit", size: "small" }}>
      {files.map((file, i) => (
        <Box key={i} pad="medium" direction="row-responsive">
          <Card
            title=""
            subTitle={
              <Anchor
                onClick={() => {
                  dispatch(showDoc(file.url));
                  dispatch(navTo({ main: "dashdoc", side: "dash" }));
                }}
              >
                {file.name.replace(/-|_/g, " ").substring(0, 25)}
              </Anchor>
            }
            align="start"
            children={null}
            footer={
              <Box>
                <Chat />
                <Text>{file.messages}</Text>
              </Box>
            }
            size="small"
          />
        </Box>
      ))}
    </Grid>
  );
};
